import Vue from 'vue'
import Router from 'vue-router'
import firebase from 'firebase'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    { 
     name: 'index',
     path: '/',
     component: () => import('./views/Index.vue'),
      // ALL PARAMATERS MUST BE CONTAINER => TO, FROM AND NEXT NO MATTER WHAT -> OTHERWISE IT CAUSES BUGS
      beforeEnter: (to, from, next) => {
      if(firebase.auth().currentUser) {
        next({ name: 'mynotes' })
      } else {
        next()
      }
    } 
    },
    { 
      name: 'mynotes', 
      path: '/mynotes', 
      component: () => import('./views/MyNotes.vue'),
      // ALL PARAMATERS MUST BE CONTAINER => TO, FROM AND NEXT NO MATTER WHAT -> OTHERWISE IT CAUSES BUGS
      beforeEnter: (to, from, next) => {
        if(firebase.auth().currentUser) {
          next()
        } else {
          next({ name: 'index' })
        }
      }
    },
    {
      name: 'settings',
      path: '/settings', 
      component: () => import('./views/Settings.vue'),
      // ALL PARAMATERS MUST BE CONTAINER => TO, FROM AND NEXT NO MATTER WHAT -> OTHERWISE IT CAUSES BUGS
      beforeEnter: (to, from, next) => {
        if(firebase.auth().currentUser) {
          next()
        } else {
          next({ name: 'index' })
        }
      } 
    },
    {
      path: '*', 
      // ALL PARAMATERS MUST BE CONTAINER => TO, FROM AND NEXT NO MATTER WHAT -> OTHERWISE IT CAUSES BUGS
      beforeEnter: (to, from, next) => {
        if(firebase.auth().currentUser) {
          next({ name: 'mynotes' })
        } else {
          next({ name: 'index' })
        }
      } 
    }
  ]
})

export default router
