<template>
      <v-toolbar :class="{'white--text': !auth, primary: !auth}" :color="$store.state.currentTheme">
      <v-toolbar-title><router-link :to="{ name: 'index' }" :class="{'white--text': lightText}" class="remove-anchor-styling">Notify</router-link></v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items>
        <v-btn flat v-if="$store.state.user" class="text-capitalize"  :class="{'white--text': lightText}" :to="{ name: 'mynotes'}">My Notes</v-btn>
      <v-menu offset-y v-if="$store.state.user">
      <template v-slot:activator="{ on }">
        <v-btn
          v-on="on"
          flat
          class="text-capitalize"
          :class="{'white--text': lightText}"
        >
          My Account
        </v-btn>
      </template>
      <v-list>
        <v-list-tile :to="{ name: 'settings'}">
          <v-list-tile-title class="black--text">Settings</v-list-tile-title>
        </v-list-tile>
        <v-list-tile @click="signOut()">
          <v-list-tile-title class="black--text">Logout</v-list-tile-title>
        </v-list-tile>
      </v-list>
    </v-menu>
      </v-toolbar-items>
    </v-toolbar>
</template>

<script>
import firebase from "firebase"
import './../../node_modules/nprogress/nprogress.css'
import NProgress from 'nprogress'

export default {

    data() {
      return {
        auth: null
      }
    },

    created() {
        firebase.auth().onAuthStateChanged(user => {
        if (user) {
            this.$store.state.user = user
            this.auth = true
        } else {
            this.$store.state.user = null
            this.auth = null
        }
        });
    },

    computed: {
      lightText() {
        if(this.$store.state.currentTheme == 'blue' || this.$store.state.currentTheme == 'green' || this.$store.state.currentTheme == 'red' || this.$store.state.currentTheme == 'grey') {
          return true
        } else {
          return false
        }
      }
    },

    methods: {
        signOut() {
        NProgress.start()
        firebase.auth().signOut().then(() => {
            this.$store.state.user = null
            this.$store.state.currentTheme = null
            this.$router.push({ name: 'index' })
            NProgress.done()
        })
        }
    }
  }
</script>
